.about {
    width: 100vw;
    height: auto;
    min-height: 100vh;
    overflow: hidden;
    background: radial-gradient(ellipse at bottom, #4d76a5 0%, #090a0f 100%);
    color: silver;
    font-size: 1.3em;
    display: flex;
    align-items: center;
    padding-left: 10vw;
    padding-bottom: 10vw;
}

.about .container {
    margin-top: 10vh;
}

#van-photo {
    width: 35vh;
    height: 30vh;
    content: url('../static/img/vancouver.jpg');
    background-position: cover;
    border-radius: 30%;
    margin-left: 50px;
}

#van-photo:hover {
    content: url('../static/img/gym.jpeg');
}

#van-photo:active {
    content: url('../static/img/food.jpeg');
}

.about button {
    background-color: azure;
    border-radius: 30%;
    border: 2px solid white;
    margin: 10px;
}

.about button:hover {
    background-color:#acd5ec;
}